/* jshint esversion: 6 */
/* eslint-disable */

import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import {indigo200} from 'material-ui/styles/colors';
import * as firebase from 'firebase';
import './Login.css';
import {textFieldStyle} from './LoginCompletingStyles';


class SignUpSection extends Component {

	constructor(props) {
		super(props);

		this.state = {
			email: "",
			password: "",
			passwordRpt: "",
			firstName: "",
			lastName: ""
		}

		// This binding is necessary to make `this` work in the callback
    	this.signUp = this.signUp.bind(this);    
	}

	signUp() {
		firebase.auth().createUserWithEmailAndPassword(this.state.email, this.state.password).catch(function(error) {
			// Handle Errors here.
			var errorCode = error.code;
			var errorMessage = error.message;
			
			// Adding user data to db
			// fetch('/api/users', {
			// 	method: 'PUT',
				
			// })
		});
	}

	validatePassword(event) {
		
	}

	render() {

		var underlineStyle = textFieldStyle.underline;
    	var floatingTextStyle = textFieldStyle.floatingLabel;

		return (
			<div style={{textAlign: 'left'}}>
				<h4>Sign Up</h4>
				<TextField
					className="Login-input"
					floatingLabelText="First Name" 
					underlineFocusStyle={underlineStyle} 
            		floatingLabelStyle={floatingTextStyle}
					onChange={(event) => this.setState({firstName: event.target.value})}
					value={this.state.firstName}
				/>
				<TextField
					className="Login-input"
					floatingLabelText="Last Name" 
					underlineFocusStyle={underlineStyle} 
            		floatingLabelStyle={floatingTextStyle}
					onChange={(event) => this.setState({lastName: event.target.value})}
					value={this.state.lastName}
				/>
				<TextField
					className="Login-input"
					floatingLabelText="Email" 
					underlineFocusStyle={underlineStyle} 
            		floatingLabelStyle={floatingTextStyle}
					onChange={(event) => this.setState({email: event.target.value})}
					value={this.state.email}
				/>
				<TextField
					className="Login-input"
					floatingLabelText="Password"
					type="password"
					underlineFocusStyle={underlineStyle} 
            		floatingLabelStyle={floatingTextStyle}
					onChange={this.validatePassword.bind(this)}         
					value={this.state.password}          
				/>
				<TextField
					className="Login-input"
					floatingLabelText="Repeat password"
					type="password"
					underlineFocusStyle={underlineStyle} 
            		floatingLabelStyle={floatingTextStyle}
					onChange={this.validatePassword.bind(this)}       
					value={this.state.password}          
				/>
				<RaisedButton 
					label="Sign Up" 
					fullWidth={true}
					primary={true}
					onClick={this.signUp} 
				/>
          </div>	
		)
	}
}

export default SignUpSection;


